@use "../_setting" as s;

#wrapper {
  .mainVisual {
    display: block;
    background: #c4c4c4;
    //background: url(../img/sample/bgMainVisual.jpg) repeat-x center top;
    //margin:0 auto calc(s.$spaceSize/2);
    @include s.media(m) {
    }
    img {
      width: 100%;
    }
    .inBase {
      position: relative;
    }
    &.--carType01.--typeWide {
      .mainVisual__carName span figure {
        width: 7.5em;
        margin-top: -1.8em;
        margin-left: -4em;
        @include s.media(m) {
          width: 11.9em;
          margin-top: -1.5em;
          margin-left: -4em;
        }
      }
    }
    &.--carType02 {
      .mainVisual__copy {
        background: s.$colorType04;
        .mainVisual__copy__inner::before,
        .mainVisual__copy__inner::after {
          background: s.$colorType04;
        }
      }
      .mainVisual__carName span figure {
        width: 10em;
        margin-top: -1.8em;
        margin-left: -5em;
        @include s.media(m) {
          width: 11.9em;
          margin-top: -1em;
          margin-left: -3em;
        }
      }
    }
    &.--carType03 {
      .mainVisual__copy {
        background: s.$colorType05;
        .mainVisual__copy__inner::before,
        .mainVisual__copy__inner::after {
          background: s.$colorType05;
        }
      }
      .mainVisual__carName span figure {
        width: 6em;
        margin-top: -1.8em;
        margin-left: -3em;
        @include s.media(m) {
          width: 8em;
          margin-top: -1.2em;
          margin-left: -1.5em;
        }
      }
    }
    &.--carType04 {
      .mainVisual__copy {
        background: s.$colorType06;
        .mainVisual__copy__inner::before,
        .mainVisual__copy__inner::after {
          background: s.$colorType06;
        }
      }
      .mainVisual__carPerson {
        @include s.media(m) {
          right: -1em;
          bottom: 7em;
        }
        @include s.media(xl) {
          bottom: 8em;
        }
      }
      .mainVisual__carName span figure {
        margin-top: -1.8em;
        @include s.media(m) {
          margin-top: -1.1em;
        }
      }
    }
    &.--carType05 {
      .mainVisual__copy {
        background: s.$colorType07;
        .mainVisual__copy__inner::before,
        .mainVisual__copy__inner::after {
          background: s.$colorType07;
        }
      }
      .mainVisual__carName span figure {
        width: 8em;
        margin-top: -1.9em;
        margin-left: -4em;
        @include s.media(m) {
          margin-top: -1em;
          margin-left: -1.5em;
        }
      }
      .mainVisual__carPerson {
        bottom: 6em;
        @include s.media(m) {
          right: -1em;
          bottom: 6em;
        }
      }
    }
    &.--carType06 {
      .mainVisual__copy {
        background: s.$colorType08;
        .mainVisual__copy__inner::before,
        .mainVisual__copy__inner::after {
          background: s.$colorType08;
        }
      }
      .mainVisual__carName span figure {
        width: 14em;
        margin-top: -1.5em;
        margin-left: -7em;
        @include s.media(m) {
          margin-top: -0.5em;
          margin-left: -3.5em;
        }
      }
    }
    &.--typeWide {
      .mainVisual__car {
        top: 23%;
        @include s.media(m) {
          width: 48em;
          top: 3em;
        }
      }
      .mainVisual__carPerson {
        @include s.media(m) {
          width: 25em;
          top: 7em;
        }
      }
      .mainVisual__copy {
        width: 85%;
        top: 1em;
        @include s.media(m) {
          width: 46.5em;
          top: 7.9em;
        }
        @include s.media(xl) {
          width: 46.4em;
        }
        .mainVisual__copy__inner {
          @include s.media(m) {
            padding-left: 4em;
          }
        }
        .mainVisual__copy__inner::before {
          @include s.media(m) {
            width: 11.9em;
            right: -11.85em;
          }
        }
        h2 span {
          &:nth-child(1) {
            font-size: 1.8em;
            @include s.media(m) {
              font-size: 2.5em;
            }
          }
          &:nth-child(2) {
            font-size: 1.2em;
            @include s.media(m) {
              font-size: 1.825em;
            }
          }
        }
      }
      .mainVisual__person {
        width: 5em;
        left: -1em;
        @include s.media(m) {
          width: 10em;
          left: 3.5em;
          bottom: 6.5em;
        }
        figure:nth-child(2) {
          width: 6em;
          top: -4em;
          right: -1.5em;
          @include s.media(m) {
            width: 9em;
            top: 1em;
            right: -8.5em;
          }
        }
      }
    }
    &__inner {
      display: block;
      width: 112%;
      margin-left: -6%;
      @include s.media(m) {
        width: 130%;
        margin-left: -15%;
      }
      @include s.media(m) {
        width: 160%;
        margin-left: -30%;
      }
    }
    &__bg {
      display: block;
      &::before {
        content: "";
        display: block;
        width: 85%;
        height: 65%;
        background: #c4c4c4;
        position: absolute;
        bottom: 0;
        right: -6%;
        clip-path: polygon(0 100%, 100% 0, 100% 100%);
        @include s.media(m) {
          content: none;
        }
      }
    }
    &__car {
      width: 95%;
      position: absolute;
      top: 20%;
      right: -10%;
      z-index: 1;
      @include s.media(m) {
        width: 40em;
        top: 0;
        right: 0;
      }
    }
    &__carPerson {
      width: 11.5em;
      position: absolute;
      bottom: 8em;
      right: -1em;
      display: block;
      z-index: 1;
      @include s.media(m) {
        width: 19em;
        bottom: 7em;
        right: 1em;
      }
    }
    &__manufacturer {
      width: 10em;
      position: absolute;
      top: 1.7em;
      right: 0;
      background: #fff;
      padding: 0.3em 0.5em;
      display: none;
      @include s.media(m) {
        right: 7.7em;
        right: 0;
        display: block;
      }
    }
    &__copy {
      width: 70%;
      display: block;
      position: absolute;
      top: 2em;
      left: 0;
      background: s.$colorType03;
      @include s.media(m) {
        width: 32.6em;
        top: 7.9em;
        left: 0;
      }
      &__inner {
        display: block;
        position: relative;
        @include s.media(m) {
          padding-left: 8em;
        }
        &::before {
          content: "";
          display: block;
          width: 5em;
          height: 100%;
          background: s.$colorType03;
          clip-path: polygon(0 0, 100% 0, 0 100%);
          position: absolute;
          top: 0;
          right: -4.95em;
          @include s.media(m) {
            width: 7.9em;
            right: -7.85em;
          }
        }
        &::after {
          content: "";
          display: block;
          width: 50vw;
          height: 100%;
          background: s.$colorType03;
          position: absolute;
          top: 0;
          left: 0.1em;
          margin-left: -50vw;
        }
      }
      h2 {
        padding: 0.8em 0;
        line-height: 1.7em;
        @include s.media(m) {
          padding: 0.5em 0;
          line-height: 3em;
        }
      }
      span {
        @include s.fontType01(Bold);
        display: block;
        color: #fff;
        letter-spacing: 0.1em;
        &:nth-child(1) {
          font-size: 1.4em;
          @include s.media(m) {
            font-size: 2.25em;
          }
        }
        &:nth-child(2) {
          font-size: 1em;
          @include s.media(m) {
            font-size: 1.5em;
          }
        }
      }
      p {
        @include s.fontType01(Bold);
        display: block;
        font-size: 0.81em;
        color: #fff;
        letter-spacing: 0.1em;
        position: relative;
        padding: 0.5em 0 2em 0;
        @include s.media(m) {
          padding: 0.5em 0 1em 0;
        }
        &::before {
          content: "";
          display: block;
          width: 95%;
          height: 1px;
          background: #fff;
          position: absolute;
          top: 0;
          left: 0;
          @include s.media(m) {
            width: 35em;
          }
        }
      }
    }
    &__carName {
      width: 112%;
      display: block;
      position: absolute;
      bottom: 1.6em;
      left: -6%;
      background: #fff;
      @include s.media(m) {
        width: 70em;
        left: 0;
      }
      span {
        width: 100%;
        display: block;
        position: relative;
        &::before {
          @include s.media(m) {
            content: "";
            display: block;
            width: 50vw;
            height: 100%;
            background: #c4c4c4;
            position: absolute;
            top: 0;
            left: 0.1em;
            margin-left: -50vw;
          }
        }
        &::after {
          @include s.media(m) {
            content: "";
            display: block;
            width: 5.2em;
            height: 100%;
            background: #c4c4c4;
            clip-path: polygon(0 0, 100% 0, 0 100%);
            position: absolute;
            top: 0;
            left: 0;
          }
        }
      }
      figure {
        width: 11.9em;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -1.4em;
        margin-left: -5.85em;
        @include s.media(m) {
          margin-left: -3em;
          margin-top: -0.7em;
        }
      }
      p {
        text-align: center;
        font-size: 1.1em;
        @include s.fontType01(Bold);
        padding: 2.5em 0 0.4em 0;
        @include s.media(m) {
          font-size: 1.25em;
          padding: 1em 3em 1em 0;
          text-align: right;
        }
        &::before {
          @include s.media(m) {
            content: "";
            display: block;
            width: 5.2em;
            height: 100%;
            background: #fff;
            clip-path: polygon(0 0, 100% 0, 0 100%);
            position: absolute;
            top: 0;
            right: -5.1em;
          }
        }
      }
    }
    &__person {
      width: 7em;
      display: block;
      position: absolute;
      bottom: 6.7em;
      left: -1em;
      @include s.media(m) {
        width: 12.56em;
        bottom: 0;
        left: 6.25em;
      }
      span {
        display: block;
        position: relative;
      }
      figure {
        &:nth-child(2) {
          width: 7em;
          position: absolute;
          top: -5em;
          right: -1em;
          @include s.media(m) {
            width: 9.1em;
            position: absolute;
            top: 0;
            right: -7em;
          }
        }
      }
    }
    &__contactBox {
      display: none;
      width: 25em;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;
      @include s.media(m) {
        display: block;
      }
      > span {
        display: block;
        background: s.$colorType02;
        border-radius: 1em 1em 0 0;
      }
      p {
        color: #fff;
        text-align: center;
        @include s.fontType01(Bold);
        font-size: 1.125em;
        padding: 0.5em 0;
      }
    }
    &__contactButton {
      display: block;
      text-align: center;
      padding: 0 0.3em 0.3em 0.3em;
      a {
        display: block;
        background: #fff;
        @include s.fontType01(Bold);
        color: s.$colorType02;
        padding: 0.9em 1em;
        font-size: 1.125em;
        text-decoration: none;
        position: relative;
        &:hover {
          background: #eee;
        }
        span {
          display: inline-block;
          width: 1.1em;
          position: absolute;
          top: 50%;
          left: 50%;
          margin-top: -0.4em;
          margin-left: -5em;
          @include s.media(xxl) {
            width: 1.3em;
          }
          img {
            width: 100%;
          }
        }
        strong {
          @include s.fontType01(Bold);
          padding-left: 1.5em;
        }
      }
    }
  }
}

#container #wrapper .mainVisual__copy h2 {
  @include s.media(m) {
    line-height: 1.4;
  }
}

#wrapper .carModelNavi {
  display: block;
  background: #313131;
  padding: 1em 0;
  &__inner {
    width: calc(100% - 0.5em);
    margin: 0 auto;
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 0.7em 0.5em;
    @include s.media(m) {
      grid-template-columns: 33.3% 33.3% 33.3%;
      width: calc(87% - 1.2em);
      gap: 0.7em 0.6em;
    }
    img {
      width: 100%;
    }
  }
  &__button {
    a {
      width: 95%;
      display: flex;
      position: relative;
      background: #fff;
      overflow: hidden;
      &::before {
        content: "";
        display: block;
        width: 3.5em;
        height: 80%;
        background: #000;
        clip-path: polygon(0 100%, 100% 0, 100% 100%);
        position: absolute;
        bottom: 0;
        right: 0;
        transition: all 0.3s ease;
        @include s.media(m) {
          width: 5.2em;
        }
      }
      & > p {
        display: inline-block;
        color: #fff;
        @include s.fontType04(Bold);
        position: absolute;
        bottom: 0.5em;
        right: 0.5em;
        font-size: 0.6em;
        @include s.media(m) {
          font-size: 0.8em;
        }
      }
      &:hover {
        &::before {
          width: 200%;
          height: 200%;
        }
      }
    }
    &.--carType01 {
      a {
        &::before {
          background: s.$colorType03;
        }
      }
    }
    &.--carType02 {
      a {
        &::before {
          background: s.$colorType04;
        }
        .carModelNavi__model figure:nth-child(2) {
          padding: 0 0.5em;
        }
      }
    }
    &.--carType03 {
      a {
        &::before {
          background: s.$colorType05;
        }
        .carModelNavi__model figure:nth-child(2) {
          padding: 0 1em;
        }
      }
    }
    &.--carType04 {
      a {
        &::before {
          background: s.$colorType06;
        }
      }
    }
    &.--carType05 {
      a {
        &::before {
          background: s.$colorType07;
        }
        .carModelNavi__model figure:nth-child(2) {
          padding: 0 0.5em;
        }
      }
    }
    &.--carType06 {
      a {
        &::before {
          background: s.$colorType08;
        }
        .carModelNavi__model {
          padding-right: 0.5em;
          figure:nth-child(2) {
            @include s.media(m) {
              width: 6.9em;
            }
            img {
              margin-left: -0.3em;
              @include s.media(m) {
                margin-left: 0;
              }
            }
          }
        }
      }
    }
  }
  &__model {
    padding: 0.5em 0.5em 0 0.5em;
    @include s.media(m) {
      padding: 0.5em 1em 0 1em;
    }
    figure {
      position: relative;
      z-index: 1;
      &:nth-child(1) {
        width: 4em;
        margin-bottom: 0.1em;
        @include s.media(m) {
          width: 5em;
          margin-bottom: 0.8em;
        }
      }
      &:nth-child(2) {
        width: 5em;
        padding-left: 0.5em;
        @include s.media(m) {
          width: 6.4em;
          padding-left: 0;
        }
      }
    }
  }
  &__image {
    width: 6em;
    display: block;
    position: relative;
    @include s.media(m) {
      width: 8.8em;
    }
    img {
      margin-left: -1em;
      @include s.media(m) {
        margin-left: -1em;
      }
    }
  }
}

#wrapper .homeNewsBox {
  .inBase__inner {
    position: relative;
    &::before {
      content: "";
      display: block;
      width: 82em;
      height: 68em;
      background: rgba(#eee, 0.5);
      clip-path: polygon(0 0, 100% 0, 0 100%);
      position: absolute;
      top: 0;
      left: -22.8em;
      z-index: -1;
    }
    &::after {
      content: "";
      display: block;
      width: 50vw;
      height: 68em;
      background: rgba(#eee, 0.5);
      position: absolute;
      top: 0;
      left: -22.8em;
      margin-left: -50vw;
      z-index: -1;
    }
  }
  &__inner {
    width: 112%;
    display: block;
    background: s.$colorType02;
    color: #fff;
    @include s.fontType01(Medium);
    padding: 0.8em 8%;
    position: relative;
    margin-left: -6%;
    @include s.media(m) {
      align-items: center;
      width: 82%;
      display: flex;
      margin-left: 0;
      padding: 0.8em 0;
    }
    &::before {
      @include s.media(m) {
        content: "";
        display: block;
        width: 3.75em;
        height: 100%;
        background: s.$colorType02;
        clip-path: polygon(0 0, 100% 0, 0 100%);
        position: absolute;
        top: 0;
        right: -3.7em;
      }
    }
    &::after {
      content: "";
      display: block;
      width: 50vw;
      height: 100%;
      background: s.$colorType02;
      position: absolute;
      top: 0;
      left: 0;
      margin-left: -50vw;
    }
  }
  &__title {
    width: 5em;
    @include s.fontType04(Medium);
    line-height: 1.5;
    display: inline-block;
    position: relative;
    margin-right: 2em;
    margin-bottom: 0.3em;
    @include s.media(m) {
      margin-right: 6em;
      margin-bottom: 0;
    }
    &::before {
      content: "";
      width: 0.8em;
      height: 0.8em;
      display: block;
      background: url(../img/common/iconArrow04.svg) no-repeat center top;
      background-size: 100% auto;
      position: absolute;
      top: 0.3em;
      right: 0;
    }
  }
  &__content {
    display: block;
    width: 100%;
    @include s.media(m) {
      width: 70%;
    }
    a {
      width: 100%;
      display: flex;
      color: #fff;
      line-height: 1.5;
      @include s.media(m) {
        display: block;
      }
      span {
        width: 30%;
        display: inline-block;
        @include s.media(m) {
          width: auto;
          margin-right: 4em;
        }
        & + span {
          width: 70%;
          @include s.media(m) {
            width: auto;
            margin-right: 0;
          }
        }
      }
      &:hover {
        color: s.$colorType06;
      }
    }
  }
  &.--typeWide {
    .inBase__inner {
      &::before {
        content: "";
        display: block;
        width: 82em;
        height: 68em;
        background: rgba(#eee, 0.5);
        clip-path: polygon(0 0, 100% 0, 0 100%);
        position: absolute;
        top: 0;
        left: -17.2em;
        z-index: -1;
      }
      &::after {
        content: "";
        display: block;
        width: 50vw;
        height: 68em;
        background: rgba(#eee, 0.5);
        position: absolute;
        top: 0;
        left: -17.2em;
        margin-left: -50vw;
        z-index: -1;
      }
    }
    .homeNewsBox__inner {
      width: 112%;
      @include s.media(m) {
        width: 90%;
      }
    }
  }
  &.--carType01 {
    .homeNewsBox__inner {
      background: s.$colorType03;
      &::before {
        @include s.media(m) {
          background: s.$colorType03;
        }
      }
      &::after {
        background: s.$colorType03;
      }
    }
  }
  &.--carType02 {
    .homeNewsBox__inner {
      background: s.$colorType04;
      &::before {
        @include s.media(m) {
          background: s.$colorType04;
        }
      }
      &::after {
        background: s.$colorType04;
      }
    }
  }
  &.--carType03 {
    .homeNewsBox__inner {
      background: s.$colorType05;
      &::before {
        @include s.media(m) {
          background: s.$colorType05;
        }
      }
      &::after {
        background: s.$colorType05;
      }
    }
  }
  &.--carType04 {
    .homeNewsBox__inner {
      background: s.$colorType06;
      &::before {
        @include s.media(m) {
          background: s.$colorType06;
        }
      }
      &::after {
        background: s.$colorType06;
      }
    }
  }
  &.--carType05 {
    .homeNewsBox__inner {
      background: s.$colorType07;
      &::before {
        @include s.media(m) {
          background: s.$colorType07;
        }
      }
      &::after {
        background: s.$colorType07;
      }
    }
  }
  &.--carType06 {
    .homeNewsBox__inner {
      background: s.$colorType08;
      &::before {
        @include s.media(m) {
          background: s.$colorType08;
        }
      }
      &::after {
        background: s.$colorType08;
      }
    }
  }
}

#wrapper .homeStockBox {
  display: block;
  padding-top: 3em;
  margin-bottom: 3em;
  .inBase__inner {
    position: relative;
    &::before {
      @include s.media(m) {
        content: "";
        display: block;
        width: 35em;
        height: 30em;
        background: s.$colorType02;
        clip-path: polygon(0 0, 100% 0, 0 100%);
        position: absolute;
        top: -3.1em;
        left: -26em;
        z-index: 1;
      }
    }
    &::after {
      @include s.media(m) {
        content: "";
        display: block;
        width: 50vw;
        height: 30em;
        background: s.$colorType02;
        position: absolute;
        top: -3em;
        left: -26em;
        margin-left: -50vw;
        z-index: 1;
      }
    }
  }
  &__title {
    display: block;
    text-align: center;
    font-size: 1.5em;
    @include s.fontType01(Bold);
    margin-bottom: 0.3em;
